import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {Injectable} from '@angular/core';
import {AuthStudentService} from './authstudent.service';
import {Router} from '@angular/router';


@Injectable()
export class AuthStudentGuard implements CanActivate {

  constructor(private authStudentService: AuthStudentService,
              private router: Router){}

  canActivate(route: ActivatedRouteSnapshot,
              state: RouterStateSnapshot):
              Observable<boolean> |
              Promise<boolean> |
              boolean {
    if (this.authStudentService.isAuthStudent) {
      return true;
    } else {
      this.router.navigate(['/auth/student']);

    }

  }
}
